.remove-this-item {
  position: absolute;
  top: 8px;
  left: 25px;
  display: inline-block;
  width: 183px;
  height: 27px;
}
.frame-inner {
  left: 8px;
  background-color: var(--color-gainsboro-100);
  width: 68px;
  height: 21px;
}
.frame-inner,
.no,
.rectangle-div,
.yes {
  position: absolute;
  top: 30px;
}
.rectangle-div {
  left: 112px;
  background-color: var(--color-gainsboro-100);
  width: 79px;
  height: 21px;
}
.no,
.yes {
  left: 28px;
}
.no {
  left: 141px;
}
.remove-this-item-parent {
  position: relative;
  background-color: var(--color-white);
  width: 201px;
  height: 59px;
  overflow: hidden;
  max-width: 90%;
  max-height: 90%;
  text-align: left;
  font-size: var(--font-size-base);
  color: var(--color-black);
  font-family: var(--font-inter);
}
